import React, { useState, useMemo, useEffect } from "react";
import {
  ConnectionProvider,
  WalletProvider,
} from "@solana/wallet-adapter-react";
import { PhantomWalletAdapter } from "@solana/wallet-adapter-wallets";
import {
  WalletModalProvider,
  WalletMultiButton,
} from "@solana/wallet-adapter-react-ui";
import { clusterApiUrl, Connection, PublicKey } from "@solana/web3.js";
import Lobby from "./components/Lobby";
import GameComponent from "./components/GameComponent";
import CreateGameForm from "./components/CreateGameForm";
import { WalletAdapterNetwork } from "@solana/wallet-adapter-base";

// Default styles that can be overridden by your app
import "@solana/wallet-adapter-react-ui/styles.css";

type View = "lobby" | "create" | "game";

function App() {
  const endpoint = getSolanaEndpoint();
  const connection = useMemo(
    () => new Connection(endpoint, "processed"),
    [endpoint]
  );
  const wallets = useMemo(
    () => [
      new PhantomWalletAdapter(),
      // Add more wallets as needed
    ],
    []
  );
  const [selectedGame, setSelectedGame] = useState<PublicKey | null>(null);
  const [view, setView] = useState<View>("lobby");
  const [connectionError, setConnectionError] = useState<string | null>(null);

  // Check connection on mount
  useEffect(() => {
    let cancelled = false;
    async function checkConnection() {
      try {
        // getEpochInfo will throw if not reachable
        await connection.getEpochInfo();
        if (!cancelled) setConnectionError(null);
      } catch (e) {
        if (!cancelled)
          setConnectionError(
            "Cannot connect to Solana localnet. Please ensure your validator is running."
          );
      }
    }
    checkConnection();
    return () => {
      cancelled = true;
    };
  }, [connection]);

  if (connectionError) {
    return (
      <div
        style={{
          background: "#111",
          minHeight: "100vh",
          color: "#fff",
          fontFamily: "sans-serif",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <div
          style={{
            background: "#222",
            padding: 32,
            borderRadius: 12,
            boxShadow: "0 2px 16px #0008",
          }}
        >
          <h2>Connection Error</h2>
          <p>{connectionError}</p>
          <button
            style={{
              background: "#ff6600",
              color: "#fff",
              border: "none",
              borderRadius: 5,
              padding: "10px 24px",
              fontWeight: "bold",
              marginTop: 16,
            }}
            onClick={() => window.location.reload()}
          >
            Retry
          </button>
        </div>
      </div>
    );
  }

  return (
    <ConnectionProvider endpoint={connection.rpcEndpoint}>
      <WalletProvider wallets={wallets} autoConnect>
        <WalletModalProvider>
          <div
            style={{
              background: "#111",
              minHeight: "100vh",
              color: "#fff",
              fontFamily: "sans-serif",
            }}
          >
            <div style={{ maxWidth: 900, margin: "0 auto", padding: 32 }}>
              <h1 style={{ textAlign: "center" }}>Solana Tic-Tac-Toe</h1>
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  marginBottom: 24,
                }}
              >
                <WalletMultiButton />
              </div>
              {view === "lobby" && (
                <Lobby
                  onSelectGame={(pda) => {
                    setSelectedGame(pda);
                    setView("game");
                  }}
                  onCreateGame={() => setView("create")}
                />
              )}
              {view === "create" && (
                <CreateGameForm
                  onGameCreated={(gamePda) => {
                    setSelectedGame(gamePda);
                    setView("game");
                  }}
                  onBack={() => setView("lobby")}
                />
              )}
              {view === "game" && selectedGame && (
                <GameComponent
                  gamePda={selectedGame}
                  onBack={() => setView("lobby")}
                />
              )}
            </div>
          </div>
        </WalletModalProvider>
      </WalletProvider>
    </ConnectionProvider>
  );
}

function getSolanaEndpoint() {
  const network = process.env.REACT_APP_SOLANA_NETWORK;
  if (network === "localnet") {
    return "http://127.0.0.1:8899";
  } else if (
    Object.values(WalletAdapterNetwork).includes(
      network as WalletAdapterNetwork
    )
  ) {
    // Safe to cast
    return clusterApiUrl(network as WalletAdapterNetwork);
  } else {
    // fallback
    return clusterApiUrl(WalletAdapterNetwork.Devnet);
  }
}

export default App;
